
body {
  font-family: sans-serif;
}

.vue-input-tag-wrapper {
		-webkit-appearance: textfield;
		background-color: #fff;
		border: 0px;
		/* cursor: text; */
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		/* flex-wrap: wrap; */
		overflow: hidden;
		/* padding-left: 4px; */
		/* padding-top: 4px; */
		/* text-align: left; */
}
.vue-input-tag-wrapper .new-tag {
		background: transparent;
		border: none;
		border-bottom: 3px solid #000;
		color: #000;
		outline: none;
		padding-left: 10px;
		width: 100%;
}
.vue-input-tag-wrapper .input-tag {
		background-color: #000;
		border: 1px solid #000;
		color: #fff;
		display: inline-block;
		font-size: 13px;
		font-weight: 400;
		margin-bottom: 4px;
		margin-right: 4px;
		padding: 7px 17px
}
.vue-input-tag-wrapper .input-tag .remove{
		color: #fff;
		margin-left: 7px;
}
.mx-calendar-content .cell.actived{
		background-color:#000
}
.VueTables__search{
		display: none;
}
VueTables__limit{
		display: none;
}
.table-striped tbody tr:nth-of-type(odd) {
	background-color: #fff;
}
.table-hover tbody tr:hover {
	background-color: #fff;
}
.table thead th,
.table td {
	  border: none;
    border-bottom: 1px solid #000;
}
.search_button {
background-color: transparent;
border: 1px solid #000;
color: #000;
cursor: pointer;
height: 48px;
padding: 0 34px;
-webkit-transition: background-color .3s,
color .3s;
transition: background-color .3s,
color .3s;
border-radius: 0px;
outline: none;
}
.search_button:hover {
background-color: #000;
color: #fff;
}
.card {
border: 1px solid #000;
border-radius: 0;
}
.mx-input{
	border: 1px solid #000;
	border-radius: 0px;
	height: 48px;
}
.mx-datepicker-range,
.mx-range-wrapper,
.mx-calendar,
.mx-calendar-content,
.mx-datepicker-popup {
	width: 100%;
}
@media only screen and (min-width: 1440px) {
.mx-calendar  {
		width: 50%;
}
}
.page-item.active .page-link {
    z-index: 1;
    color: #fff;
    background-color: #000;
    border-color: #000;
}
.page-link,
.page-item.disabled .page-link {
    position: relative;
    display: block;
    padding: .5rem .75rem;
    margin-left: -1px;
    line-height: 1.25;
    color: #000;
    background-color: #fff;
		border: 1px solid #000;
		-webkit-transition: background-color 0.3s, color 0.3;
		transition: background-color 0.3s, color 0.3;
}
.page-link:hover {
    color: #fff;
    text-decoration: none;
    background-color: #000;
    border-color: #000;
}
.form-control {
	borde-radius: none!important;
}
.mx-datepicker .mx-datepicker-range {
	width: 100%;
}
